import './Break.css';
import img1 from './pic/bare1.webp';
import img2 from './pic/bare2.webp';

const BreakC: React.FC = () => {
    return <div className='break_font'>
            <div className='break_img'>
                <img src={img1}/>
            </div>
            <p>      </p>
            <p>   .    </p>
            <p>   .</p>
            <p>    ,</p>
            <p>   . </p>
            <div className="break_from">-      -</div>
            <div className='break_img'>
                <img src={img2}/>
            </div>
    </div>;
}

export default BreakC;