import './Story.css';
import StoryBoard, { AccordionContent } from './StoryBoard';
import Therapy from './Therapy/Therapy';

const notice: AccordionContent[] = [
  {
    panel: 'panel1',
    title: '   -  ',
    content: [':90', ':   ', '       ,     ,           .']
  },
  {
    panel: 'panel2',
    title: '  ',
    content: [': 90', ':      ', '    ,            .']
  },
  {
    panel: 'panel3',
    title: "'   '  ",
    content: [':      11', ': ', ':      .', ':       .      .      .']
  }
]

const qna: AccordionContent[] = [
  {
    panel: 'panel1',
    title: '   ?',
    content: ['']
  }
]

const Story: React.FC = () => {    
    return (
    <section id="story" className='story-body'>
        <h2> </h2>
        <Therapy />
        <StoryBoard title='' contents={notice}/>
        <StoryBoard title='Q & A' contents={qna}/>
    </section>
)
};

export default Story;