import { HashLink } from 'react-router-hash-link';
import { isMobile } from "react-device-detect";
import logo from "./logo.png"
import './Navigation.css';

const Navigation: React.FC = () => {
    return (
      <nav className="main-nav">
        <HashLink to="/"><img src={logo} alt="logo" className='logo' /></HashLink>
        <ul>
          <HashLink to="/main#intro"><li> </li></HashLink>
          { isMobile ? (<></>) : (<>
          <HashLink to="/main#fields"><li> </li></HashLink>
          <HashLink to="/main#program"><li>Special </li></HashLink>
          <HashLink to="/main#process"><li> </li></HashLink>
          <HashLink to="/main#story"><li> </li></HashLink></>)}
          <HashLink to="/diagnosis"><li></li></HashLink>
        </ul>
      </nav>
    );
  };

  export default Navigation;