import { useLocation } from "react-router-dom";
import Navigation from "../../common/Navigation";
import workwellimg from "./pic/workwell.jpg"
import overUserImg from "./pic/overUser.jpg"
import highUserImg from "./pic/highUser.jpg"
import "./SmartPhoneDiagnosisResult.css";

interface Result {
    type: string,
    picturePath: string,
    description: string,
}

const SmartPhoneDiagnosisResult: React.FC = () => {
    const location = useLocation();

    const results: Result[] = [
        {
            type: "normal", 
            picturePath: workwellimg, 
            description: "  .    ."
        },
        {
            type: "overUser", 
            picturePath: overUserImg, 
            description: "   .    . "
        },
        {
            type: "highUser", 
            picturePath: highUserImg, 
            description: "   .   ."
        },
    ];

    let resultIndex = 0;
    if (location.state.result <= 2.78) {
        resultIndex = 0;
    } else if (location.state.result <= 3.07) {
        resultIndex = 1;
    } else {
        resultIndex = 2;
    }

    return <div className="smart_phone_diagnosis_result_body">
        <Navigation />
        <div className="smart_phone_result_box" >
            <img src={results[resultIndex].picturePath} />
            <div>{results[resultIndex].description}</div>
        </div>
    </div>;
}

export default SmartPhoneDiagnosisResult;