import './Fields.css';
import FieldCard from './FieldCard';
import singleImg from './pic/abc.webp';
import groupImg from './pic/group.webp';
import psyTestImg from './pic/psytest.jpg';
  
const Fields: React.FC = () => {
    return (
      <section id="fields" className='fields-body'>
        <h2> </h2>
        <div className='field-card-container'>
        <FieldCard
          header=' ' 
          image={singleImg}
          content='       ,         '>
        </FieldCard>
        <FieldCard 
          header=' ' 
          image={groupImg}
          content='               ,            '>
        </FieldCard>
        <FieldCard 
          header=' ' 
          image={psyTestImg}
          content='-  10 (HTP + SCT +  50) \n
        -     20 (MMPI-2  MMPI-A + TCI +  80) \n
        -    10 (TCI + SCT +  50) \n
        -   12 (TAT + HTP +  60) \n
        -    20 (MMPI-2 + TCI + SCT +  80)'>
        </FieldCard>
        </div>
    </section>
)
};

export default Fields;